<template></template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'

export default {
  name: "",
  props: {},
  data: function() {
    // 資料
    return {
      // // 複製帳號層級代號
      // level: 0,
      // // 複製帳號上層層級代號
      // upLevel: 0,
      // 複製資料
      Items: {
        dialogOpen: false,
        valid: false,
        // 帳號
        account: {
          selected: '',
          options: []
        },
        // 名稱資料
        nameItems: {
          // 名稱
          name: '',
          nameRules: [] // *initLanguage*
          // nameRules: [
          //   v => !!v || '不能為空',
          //   v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || '不能為特殊符號或空格，除了「括號()」、「底線_」和「-」',
          //   v => v.length <= 20 || '不能超過 20 個字元'
          // ]
        },
        // 密碼資料
        passwordItems: {
          // 密碼
          password: '',
          passwordIsVisible: false,
          // 確認密碼
          passwordConfirmation: '',
          passwordConfirmationIsVisible: false,
          passwordRules: [] // *initLanguage*
          // passwordRules: [
          //   v => !!v || '不能為空',
          //   v => (v.length === 0 || v.length >= 4) || '長度需為 4 位以上',
          //   v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || '需為英文或數字',
          //   v => (!(/^[0-9]+$/.test(v)) || v === '') || '需為英文加數字組合',
          //   v => (!(/^[A-Za-z]+$/.test(v)) || v === '') || '需為英文加數字組合'
          // ]
        },
        // (體育)信用額度 [信用 / 剩餘額度]
        sportCreditItems: {
          amount: 0,
          suffixLeft: '', // *initLanguage*
          // suffixLeft: '(萬) / ',
          suffixRight: '', // *initLanguage*
          // suffixRight: ' (萬)',
          disabled: false,
          show: true
        },
        // (體育)現金額度 [現金 / 剩餘額度]
        sportCashItems: {
          amount: 0,
          suffixLeft: '', // *initLanguage*
          // suffixLeft: '(元) / ',
          suffixRight: '', // *initLanguage*
          // suffixRight: ' (元)',
          disabled: false,
          show: true
        },
        // (體育)剩餘額度
        sportSurplusQuota: 0,
        sportQuotaRules: [] // *initLanguage*
        // sportQuotaRules: [
        //   v => (v !== '') || `不能為空`,
        //   v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || `只能數字不能負數和空格`,
        //   v => (v <= parseFloat(this.Items.sportSurplusQuota)) || `要小於等於 ${this.Items.sportSurplusQuota}`
        // ]
      },
      // 複製搜尋帳號的上層帳號
      searchUpAccount: ''
    };
  },
  watch: {
    //監聽值
    // 切換語系
    '$store.state.lang' (value) {
      this.initLanguage()
    }
  },
  computed: {
    //相依的資料改變時才做計算方法
    ...mapGetters({
      getGenerateGleadAccountState: 'account-management/getGenerateGleadAccountState',
      getProfileState: 'account-management/getProfileState',
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    })
  },
  methods: {
    // 初始
    ...mapActions({
      postSuperAccount: 'api/postSuperAccount',
      getGenerateAccount: 'api/getGenerateAccount',
      getSuperAccountQuota: 'api/getSuperAccountQuota',
      getDownAccount: 'api/getDownAccount'
    }),
    toBack () {
      // 複製總監，登入大總監 (因為總監，只有在大總監登入才有)
      if (this.upLevel === 7) {
        let path = (this.$route.path).replace(/\/copy/i, '')
        this.$router.push(path)
      } else if (parseInt(sessionStorage.getItem('super.manage.level')) === this.upLevel) {
        // 複製大股東，登入(super.manage.level)為總監(upLevel)
        // 複製股東，登入(super.manage.level)為大股東(upLevel)
        // 複製總代理，登入(super.manage.level)為股東(upLevel)
        // 複製代理，登入(super.manage.level)為總代理(upLevel)
        // 複製會員，登入(super.manage.level)為代理(upLevel)
        let path = (this.$route.path).replace(/copy/i, 'directList')
        this.$router.push(path)
      } else {
        // 複製大股東，登入不為總監
        // 複製股東，登入不為大股東
        // 複製總代理，登入不為股東
        // 複製代理，登入不為總代理
        // 複製會員，登入不為代理
        let path = (this.$route.path).replace(/\/copy/i, '')
        if (this.$route.query.searchStatus && this.searchUpAccount) { // 搜尋帳號狀態
          this.$router.push({
            path: path,
            query: {
              upAccount: this.searchUpAccount
            }
          })
        } else if (this.$route.query.upAccount) {
          this.$router.push({
            path: path,
            query: {
              upAccount: this.$route.query.upAccount
            }
          })
        } else {
          this.$router.push(path)
        }
      }
    },
    add () {
      let addData = {
        copyAccount: this.$route.query.item.l_no, // 該筆記錄帳號
        l_no: this.Items.account.selected,
        l_name: this.Items.nameItems.name,
        l_pwd: this.Items.passwordItems.password,
        l_pwd_confirmation: this.Items.passwordItems.passwordConfirmation
      }

      // 上層帳號
      if (this.level < 6 && this.getProfileState.hasOwnProperty('result') && this.getProfileState.result !== undefined) {
        // 複製大股東(level)，若登入帳號(l_lv)不為總監(upLevel)，則需要送[上層帳號]
        // 複製股東(level)，若登入帳號(l_lv)不為大股東(upLevel)，則需要送[上層帳號]
        // 複製總代理(level)，若登入帳號(l_lv)不為股東(upLevel)，則需要送[上層帳號]
        // 複製代理(level)，若登入帳號(l_lv)不為總代理(upLevel)，則需要送[上層帳號]
        // 複製會員(level)，若登入帳號(l_lv)不為代理(upLevel)，則需要送[上層帳號]
        if (this.getProfileState.result.l_lv !== this.upLevel) {
          if (this.$route.query.searchStatus && this.searchUpAccount) { // 搜尋帳號狀態
            addData.upAccount = this.searchUpAccount
          } else if (this.$route.query.upAccount) {
            addData.upAccount = this.$route.query.upAccount
          }
        } else {
          // 複製大股東(level)，若登入帳號(l_lv)為總監(upLevel)，則送[登入帳號]
          // 複製股東(level)，若登入帳號(l_lv)為大股東(upLevel)，則送[登入帳號]
          // 複製總代理(level)，若登入帳號(l_lv)為股東(upLevel)，則送[登入帳號]
          // 複製代理(level)，若登入帳號(l_lv)為總代理(upLevel)，則送[登入帳號]
          // 複製會員(level)，若登入帳號(l_lv)為代理(upLevel)，則送[登入帳號]
          addData.upAccount = sessionStorage.getItem('super.manage.account')
        }
      } else if (this.level === 6) {
        // 複製總監(level)，直接送登入帳號 (因為總監，只有在大總監登入才有)
        addData.upAccount = sessionStorage.getItem('super.manage.account')
      }

      // (體育)信用額度
      if (this.Items.sportCreditItems.show && !this.Items.sportCashItems.show) {
        addData.quota_LCtotal = this.Items.sportCreditItems.amount
      // (體育)現金額度
      } else if (!this.Items.sportCreditItems.show && this.Items.sportCashItems.show) {
        addData.quota_LCtotal = this.Items.sportCashItems.amount
      }
      try {
        this.postSuperAccount(addData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`新增失敗`))
            } else if (res.status === 200) { // success
              this.$toastr.success(this.$ln(`新增成功`), '', {timeOut: 2000, closeButton: false})
              // 清除欄位資料
              this.clearField()
              // 刷新自動生成帳號編號(管端帳號)
              this.loadGenerateAccount()
              // 新增成功回上一頁
              this.toBack()
              // 清除帳號列表記憶頁數 session
              common.clearAccountListSession()
            }
          })
          .catch(err => {
            this.$toastr.error(this.$ln(`新增失敗`))
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    loadGenerateAccount () {
      let getData = {
        type: 'glead'
      }
      // 帳號
      // 載入總監(level)自動生成帳號編號，不需要送帳號 (因為總監，只有在大總監登入才有)
      // 載入大股東(level)自動生成帳號編號，若登入帳號(super.manage.level)不為總監(upLevel)則需要送帳號
      // 載入股東(level)自動生成帳號編號，若登入帳號(super.manage.level)不為大股東(upLevel)則需要送帳號
      // 載入總代理(level)自動生成帳號編號，若登入帳號(super.manage.level)不為股東(upLevel)則需要送帳號
      // 載入代理(level)自動生成帳號編號，若登入帳號(super.manage.level)不為總代理(upLevel)則需要送帳號
      // 載入會員(level)自動生成帳號編號，若登入帳號(super.manage.level)不為代理(upLevel)則需要送帳號
      if (parseInt(sessionStorage.getItem('super.manage.level')) !== this.upLevel && this.level < 6) {
        if (this.$route.query.searchStatus && this.searchUpAccount) { // 搜尋帳號狀態
          getData.l_no = this.searchUpAccount
        } else if (this.$route.query.upAccount) {
          getData.l_no = this.$route.query.upAccount
        }
      }
      try {
        this.getGenerateAccount(getData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得可新增管端帳號失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得可新增管端帳號成功`)
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    clearField () {
      this.Items.nameItems.name = ''
      this.Items.passwordItems.password = ''
      this.Items.passwordItems.passwordConfirmation = ''
      this.Items.sportCreditItems.amount = ''
      this.Items.sportCashItems.amount = ''
    },
    // 取得預設資料
    getDefault () {
      // 取得額度預設值
      try {
        let getData = {}
        // 帳號
        // 新增總監，不需要送帳號 (因為總監，只有在大總監登入才有)
        // 新增大股東，若登入帳號(super.manage.level)不為總監(upLevel)，則需要送上層帳號
        // 新增股東，若登入帳號(super.manage.level)不為大股東(upLevel)，則需要送上層帳號
        // 新增總代理，若登入帳號(super.manage.level)不為股東(upLevel)，則需要送上層帳號
        // 新增代理，若登入帳號(super.manage.level)不為總代理(upLevel)，則需要送上層帳號
        // 新增會員，若登入帳號(super.manage.level)不為代理(upLevel)，則需要送上層帳號
        if (parseInt(sessionStorage.getItem('super.manage.level')) !== this.upLevel && this.level < 6) {
          if (this.$route.query.searchStatus && this.searchUpAccount) { // 搜尋帳號狀態
            getData.l_no = this.searchUpAccount
          } else if (this.$route.query.upAccount) {
            getData.l_no = this.$route.query.upAccount
          }
        }
        this.getSuperAccountQuota(getData)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得額度預設值失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得帳號 ${this.$route.query.upAccount} 額度預設值成功`, res.data.result)
                // 設置(體育)剩餘額度值
                if (res.data.result.hasOwnProperty('quota') && res.data.result.quota.hasOwnProperty('sport')) this.Items.sportSurplusQuota = res.data.result.quota.sport.surplus
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo &&
          dashboardInfo.hasOwnProperty('wsType')) {
        /* 註: 先更新站台種類設置再更新開放遊戲設定 */
        // 依照站台種類更新輸入欄(1:現金、2:信用)
        switch (dashboardInfo.wsType) {
          case 1:
            this.Items.sportCashItems.show = true
            this.Items.sportCreditItems.show = false
            break
          case 2:
            this.Items.sportCreditItems.show = true
            this.Items.sportCashItems.show = false
            break
        }
      }
    },
    // 初始語系
    initLanguage () {
      // 名稱資料
      this.Items.nameItems.nameRules = [
        v => !!v || this.$ln(`不能為空`),
        v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || this.$ln(`不能為特殊符號或空格，除了「括號()」、「底線_」和「-」`),
        v => v.length <= 20 || this.$ln(`不能超過 20 個字元`)
      ]

      // 密碼資料
      this.Items.passwordItems.passwordRules = [
        v => !!v || this.$ln(`不能為空`),
        v => (v.length === 0 || v.length >= 4) || this.$ln(`長度需為 4 位以上`),
        v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || this.$ln(`需為英文或數字`),
        v => (!(/^[0-9]+$/.test(v)) || v === '') || this.$ln(`需為英文加數字組合`),
        v => (!(/^[A-Za-z]+$/.test(v)) || v === '') || this.$ln(`需為英文加數字組合`)
      ]

      // (體育)信用額度 [信用 / 剩餘額度]
      this.Items.sportCreditItems.suffixLeft = `(${this.$ln('萬')}) / `
      this.Items.sportCreditItems.suffixRight = ` (${this.$ln('萬')})`

      // (體育)現金額度 [現金 / 剩餘額度]
      this.Items.sportCashItems.suffixLeft = `${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))} / `
      this.Items.sportCashItems.suffixRight = ` ${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}`

      // (體育)剩餘額度
      this.Items.sportQuotaRules = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v <= parseFloat(this.Items.sportSurplusQuota)) || this.$ln(`要小於等於 %s`, this.Items.sportSurplusQuota)
      ]
    }
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 $data 已可取得，但 $el 屬性還未被建立。

  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    //元素已掛載， $el 被建立。
    this.initLanguage()
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    //實體還可使用。
  },
  destroyed: function() {
    //實體銷毀。
  }
  //END--生命週期
};
</script>

<style lang="scss" scoped>
</style>
